h1 {
    margin-top: 20px;
    color: #637175;
    letter-spacing: 2px;
    text-align: center;
}
.content{
    background: #fff;
    padding:  20px 30px;
    box-shadow: 1px 3px 5px rgba(0,0,0,0.1)
}
form input:focus{
    outline: none;
    padding-bottom: 8px;
    transition: all ease 0.2s;
}
form:after{
    content: '';
    clear: both;
    display: block;
}
.taskB {
    background-color:#6e6d6d;
    border: none;
    outline: none;
    color: white;
    margin: 10px;
}
.taskB:hover {
    background-color: gray;
}
.remove {
  background-color: pink;
  border-radius: 10px;
  border: 2px solid white;
  float: right;
}
.note-title {
  border: 1px solid#6e6d6d;
  border-radius: 15px;
  margin-left: 275px;
  margin-right: 275px;
  margin-top: 20px;
  float: center;
}
.taskContent {
  text-align: center;
  padding: 5px;
  color: rgb(78, 76, 76);
}
#add-task {
    text-align: right;
}