form {
    padding: 20px;
    margin-top: 20px;
    text-align: center;
    border: solid 3px pink;
}
.title, .login {
    padding-bottom: 20px;
}
.btn {
    background-color:#6e6d6d;
    border: none;
    outline: none;
}
.btn:hover {
    background-color: gray;
}
input {
    border: 1px solid gray;
    border-radius: 5px;
}
::placeholder {
    text-align: center;
}